import React, {useEffect} from 'react'
import Monitoring from "./index";
import {Switch} from "react-router-dom";
import UseAuthComponent from "../../components/permission-components/use-auth.component";
import PrivateRoute from "../../permissions/PrivateRoute";
import {connect} from "react-redux";
import {_GetMonitoring} from "../../redux/actions/Monitoring";
import {SocketConnect, SocketDisconnect} from "../../redux/actions/websocket.action";
import TableComponent from "./components/table.component";

export default function MonitoringApp(props) {
    return (
        <div className={'yid-monitoring'}>
            <Switch>
                <PrivateRoute path={'/'} component={App}/>
            </Switch>
        </div>
    )
}

const App = connect(() => {
    return {}
}, {})(function (props) {


    return (
        <UseAuthComponent loading={{title: "Monitoring App", message: "Harap tunggu! sedang melakukang pengecekan"}}>
            <Monitoring.Layout>
                <Monitoring.Content>
                    {({active}) => {
                        return <Views
                            title={active ?? "arrivals"}
                            type={active}
                            pagination={false}
                        />
                    }}
                </Monitoring.Content>
            </Monitoring.Layout>
        </UseAuthComponent>
    )
})

const Views = connect(({Monitoring}) => {
    return { ...Monitoring }
}, {_GetMonitoring, SocketConnect, SocketDisconnect})(function (props) {
    let {type, title, _GetMonitoring, SocketConnect, SocketDisconnect} = props

    useEffect(()=> {
        if(type){
            _GetMonitoring({
                type,
                params: {
                    page:1,
                    limit:20
                }
            })
        }
    },[type,_GetMonitoring])

    return <TableComponent {...props}/>
})