import React, {useState, useEffect} from 'react'
import {Button, Card, Table, Tag, Tooltip} from "antd";
import { FilterOutlined } from '@ant-design/icons'
import AvatarStatus from "components/shared-components/AvatarStatus";
import PropTypes from "prop-types";
import { connect } from 'react-redux'
import moment from "moment";


function TableComponent(props){
    let { title,data,loading } = props

    function  ExtraComponent(){
        return (
            <div className="text-right">
                <Tooltip title={'Filterd Data'}>
                    <Button
                        type={'primary'}
                        ghost
                        size={'small'}
                        icon={<FilterOutlined/>}/>
                </Tooltip>
            </div>
        )
    }

    const columns = [
        {
            dataIndex:"date",
            title:"Date",
            ellipsis: true,
            render:(_)=> _ ? moment(_).format("DD/MM/YYYY"): "-"
        },
        {
            dataIndex: "airline",
            title:"Airlines",
            width:220,
            ellipsis: true,
            render:(_)=> {
                return (
                    <>
                        <AvatarStatus
                            subTitle={_?.data?.code ?? ""}
                            src={'/img/avatars/thumb-1.jpg'}
                            name={_?.data?.name ?? "-"}

                        />
                    </>
                )
            }
        },
        {
            ellipsis: true,
            dataIndex: "airline",
            title:"Airlines",
            render:(_)=> {
                return _?.data?.name ?? "-"
            }
        },
        {
            dataIndex: "flight_no",
            title:"Flight No",
            ellipsis: true,
            render:(_)=> <Tag color={'blue'}>{_ ?? ""}</Tag>
        },
        {
            ellipsis: true,
            title:"Time",
            children: [
                {
                    // width:120,
                    ellipsis:true,
                    dataIndex:"schedule_time",
                    title: "Schedule",
                },
                {
                    // width:120,
                    ellipsis:true,
                    dataIndex:"actual_time",
                    title: "Actual",
                },

            ]
        },
        {
            dataIndex: "status",
            title:"STATUS",
            render: (_)=> `${_}`.toUpperCase()
        }
    ]


    return (
        <div className="w-full">
            <Card title={title} extra={<ExtraComponent/>} className={'overflow-auto'}>
                <Table
                    rowClassName={(record, index) => record?.isHighlight ?"!mk-bg-green-500 !mk-bg-opacity-20":''}
                    pagination={false}
                    columns={columns}
                    loading={loading ?? false}
                    dataSource={data ?? []}
                />
            </Card>
        </div>
    )
}



TableComponent.propTypes = {
    title: PropTypes.string.isRequired,
    params: PropTypes.shape({
        page:PropTypes.number.isRequired,
        limit: PropTypes.number.isRequired
    }),
    pagination: PropTypes.bool.isRequired
}
TableComponent.defaultProps = {
    title:null,
    pagination: false
}
export default connect(({Monitoring})=> {
    return {...Monitoring}
},{})(TableComponent)