import React, {Component, useState} from 'react';
import { connect } from 'react-redux';
import {useLocation} from "react-router-dom";
import ContainerTailwind from "../../../components/layout-components/ContainerTailwind";
import CountCard from "../../app-views/home/components/count.card";
import Monitoring from "../index";
import MonitoringSocket from "./monitoring.socket";

function mapStateToProps(state) {
 return {

 };
}

function ContentApp(props)  {
    const locations = useLocation()
    const [active, setActive] = useState(()=> {
        let value = new URLSearchParams(locations.search).get('tab')
        if (value) {
            return value.toString()?.toLowerCase()
        }
        return 'arrivals'
    })

    function setUp(value) {
        if (window) {

            const urlSearch = new URLSearchParams(window.location.search)
            if(urlSearch.get('tab')){
                urlSearch.delete('tab')
                urlSearch.append('tab', value)
            }else{
                urlSearch.append('tab', value)
            }
            let query = []
            urlSearch.forEach((value, key) => {
                query.push(`${key}=${value}`)
            })

            let newUrl = [window.origin, locations.pathname].join("")
            window.history.pushState({article: 'read-all'}, 'Read All Article', [newUrl, query.join("&")].join("?"));

        }
    }
    const onSetActive = (actived)=> {
        if(actived !== active){
            setActive(actived)
            setUp(actived)
        }
    }
    return (
        <div className="w-full !mk-overflow-x-hidden">
            <Monitoring.Navbar/>
            <div className={'!mk-mt-[70px]'}>
                <ContainerTailwind>
                    <div className={'!mk-py-10'}>
                        <CountCard active={active} onChange={onSetActive}/>
                        {props?.children({active})}
                    </div>
                </ContainerTailwind>
            </div>
        </div>
    )
}

export default connect(
 mapStateToProps,
)(ContentApp);